import React from "react";
import "./Login.css";
import PropTypes from "prop-types";
import { Form, Button, FormGroup, FormControl } from "react-bootstrap";
import API from "../../utils/API";

export default class Login extends React.Component {
	state = {
		email: "",
		errors: [],
		alert: false
	};
	componentDidMount() {
		window.scrollTo(0, 0);
	};
	send = async () => {
		const { email } = this.state;
		if (!email || email.length === 0) {
		return;
		}
		try {
			const { data } = await API.login(email);
			sessionStorage.setItem("email", data.data.email);
			window.location = "/results";
		} catch (error) {
			console.error(error);
		}
	};
	onClose = e => {
		e.stopPropagation();
		var scroll = document.getElementsByClassName("page_wrapper")[0].style.top;
		document.getElementsByClassName("page_wrapper")[0].style.position = "inherit";
		document.getElementsByTagName("html")[0].style.scrollBehavior = "initial";
		window.scrollTo(0, -parseInt(scroll));
		document.getElementsByTagName("html")[0].style.scrollBehavior = "smooth";
    	this.props.onClose && this.props.onClose(e);
  	};
	handleChange = (event) => {
		this.setState({
		[event.target.id]: event.target.value
		});
	};
	hasError(key) {
		return this.state.errors.indexOf(key) !== -1;
	};
	handleSubmit = async(event) => {
		event.preventDefault();
		var errors = [];
		const expression = /\S+@\S+/;
		var validEmail = expression.test(String(this.state.email).toLowerCase());
		if (!validEmail) {
			errors.push("email");
		}
		this.setState({
			errors: errors
		});
		if (errors.length > 0) {
			return false;
		} else {
			try {
				const { data } = await API.login(this.state.email);
				sessionStorage.setItem("email", data.data.email);
				sessionStorage.setItem("dilemme", data.data.dilemme);
				if (!data.data.infos_perso)
					window.location = "/questions";
				else if (!data.data.dilemme_reponses)
					window.location = "/dilemme";
				else if (!data.data.annexes)
					window.location = "/annexe";
				else if (!data.data.mbti.type)
					window.location = "/mbti";
				else
					window.location = "/resultats";
				console.log(data.data);
			} catch (error) {
				if (error.message === "Request failed with status code 401")
				{
					this.setState({
						alert: true
					});
				}
			}
		}
	};
  	render() {
	const { email } = this.state;
    if (!this.props.show) {
      return null;
    }
	if (window.innerWidth < 560)
	{
		var scroll = window.scrollY;
		document.getElementsByClassName("page_wrapper")[0].style.position = "fixed";
		document.getElementsByClassName("page_wrapper")[0].style.top = -scroll + "px";
	}
	
    return (
		<div id="login_wrapper">
			<div id="login">
				{ this.props.closable && <span className="closebtn" onClick={this.onClose}>&times;</span> }
				<h2 className="login_title">Se connecter</h2>
				<div className="content">
					{ this.props.children }
					<div id="login_prez">Vous avez <b>d√©j√† r√©pondu</b> et souhaitez retrouver le r√©sultat de votre test de personnalit√© ?<br/>Vous avez commenc√© √† r√©pondre mais n'avez <b>pas fini</b> ?<br/>Connectez-vous ci-dessous pour acc√©der <b>aux r√©sultats</b> o√π √† vos <b>questions incompl√®tes</b>.</div>
					<Form onSubmit={this.handleSubmit}>
						<FormGroup controlId="email">
						<FormControl
							autoFocus
							placeholder="Email"
							value={email}
							onChange={this.handleChange}
							className={
								this.hasError("email")
								  ? "form-control is-invalid"
								  : "form-control"
							  }
						/>
						<Form.Control.Feedback type="invalid" >
							<i className="fas fa-exclamation-circle"></i> Email invalide.
            			</Form.Control.Feedback>
						{this.state.alert && 
						<div id="user_not_exist">
							Cette adresse mail ne correspond √† aucune entr√©e. <a href="/questions">Commencer le questionnaire</a> ?
						</div> }
						</FormGroup>
						<Button className="next_button" block type="submit">
						Connexion
						</Button>
					</Form>
					{ this.props.closable && <Button id="back_button" onClick={this.onClose}>Retour</Button>}
					{ !this.props.closable && <Button id="back_button" onClick={() => window.location = "/questions"}>Retour au d√©but</Button>}
				</div>
			</div>
	  	</div>
    );
  }
}
Login.propTypes = {
  show: PropTypes.bool.isRequired,
  closable: PropTypes.bool.isRequired
};
